.lzAppContainer{
    position:relative;
    display: flex;
}

.lzMainContainer{
    position:relative;
    width:760px;
    background-color: var(--champagneSorbet);
}

@media(max-width:600px){
    .lzAppContainer{
        position:relative;
        display: flex;
        flex-direction: column;
        overflow-y: hidden;
    }

    .lzMainContainer{
        position:relative;
        width:100vw;
        background-color: var(--champagneSorbet);
    }
} 