@media (min-width:601px){
    .lzCategoryBox{
        background-color: var(--lambsEar);
        position: sticky;
        top:0;
        width:200px;
        height:100vh;
        font-family: GmarketSans;
    }
    .lzCategoryBox__iconsList{
        font-size:24px;
        width:100%;
        display: flex;
        justify-content: end;
        align-items: center;
        padding:20px;
        gap:8px;
    }

    .lzCategoryBox__icon{
        text-decoration: none;
        color:var(--champagneSorbet);
    }

    .lzCategoryBox__icon--board{
        font-size:20px;
    }
    .lzCategoryBox__icon:hover{
        color:var(--chamomile);
    }

    .lzCategory{
        padding-left:20px;
    }
    .lzCategory--mobile{
        display: none;
    }
    .lzStudyList{
        display: flex;
        flex-direction: column;
        padding-left:20px;
        margin-bottom:12px;
    }

    .lzStudyList__item{   
        text-decoration: none;
        color:gray;
        margin:4px;
    }

    .lzStudyList__item:hover{   
        color:var(--chamomile);
    }
}

@media (max-width:600px) {
    .lzCategoryBox{
        position: absolute;
        bottom:0;
        width:100vw;
        z-index: 20;
        display: flex;
        font-family: GmarketSans;
        padding:20px;
    }
    .lzCategoryBox__iconsList{
        display: none;
    }
    .lzCategory__item{
        display: flex;
        flex-direction: column;
    }
    .lzCategory::-webkit-scrollbar{
        display: none;
    }
    .lzCategory{
        display: flex;
        gap:20px;
        align-items: end;
        overflow-x: scroll;
        
    }
    .lzCategory--web{
        display:none;   
    }
    .lzStudyList{
        position: absolute;
        display: flex;
        flex-wrap:wrap;
        bottom:60px;
        margin-bottom:12px;
        gap:20px;
        background: linear-gradient(#1e1e1e, rgb(31, 31, 31));
        box-shadow: 0 4px 32px 0 rgba(42, 42, 42);
        margin-right:20px;
        padding:12px;
        border-radius: 12px;
    }
    .lzStudyList__title{
        height:28px;
        background-color: rgb(253,251,213);
        display: flex;
        align-items: center;
        padding:12px;
        border-radius: 8px;
        color:#005d77;
        font-weight: bold;
    }
    .lzStudyList__item{   
        background-color: var(--chamomile);
        color:#008CA9;
        padding:12px;
        border-radius: 8px;
        text-decoration: none;
    }
}